import { nebRequire } from '../libs/nebPay'
import nebulas from 'nebulas'

var dappAddress = 'n1kTGB3Q8SeVtDCo8JuMPkK9WYbdxQSNJCq'
var nebulasServer = 'https://testnet.nebulas.io'
//var nebulasServer = 'https://mainnet.nebulas.io'

var serialNumber

var neb = new nebulas.Neb(new nebulas.HttpRequest(nebulasServer))
var NebPay = nebRequire('nebpay')
var nebPay = new NebPay()

var value = '0'
var nonce = '0'
var gasPrice = '1000000'
var gasLimit = '2000000'

function isAnonymous () {
  if (typeof (webExtensionWallet) === 'undefined') {
    return true
  }
  return false
}

export default {
  publishLove (title, message, image, callback) {
    if (isAnonymous()) {
      alert("请先安装官方钱包插件 WebExtensionWallet")
      window.open("https://github.com/ChengOrangeJu/WebExtensionWallet")
    }else {
      var func = 'save'
      var args = '[\"' + title + '\",\"' + message + '\",\"' + image + '\"]'
      nebPay.call(dappAddress, 0, func, args, {
        listener: callback
      })
    }
  },
  getByDesc (offset, limit, callback) {
    var func = 'getByDesc'
    var args = '[\"' + offset + '\",\"' + limit + '\"]'
    if (isAnonymous()) {
      var contract = {
        'function': func,
        'args': args
      }
      var from = nebulas.Account.NewAccount().getAddressString()
      neb.api.call(from, dappAddress, value, nonce, gasPrice, gasLimit, contract).then(callback)
    } else {
      nebPay.simulateCall(dappAddress, '0', func, args, {
        listener: callback
      })
    }
  },
  getSelf (callback) {
    var func = 'getSelf'
    var args = '[]'
    if (isAnonymous()) {
      var contract = {
        'function': func,
        'args': args
      }
      var from = nebulas.Account.NewAccount().getAddressString()
      neb.api.call(from, dappAddress, value, nonce, gasPrice, gasLimit, contract).then(callback)
    } else {
      nebPay.simulateCall(dappAddress, '0', func, args, {
        listener: callback
      })
    }
  }
}



