<template>
  <div class="love-oath">
    <div class="banner">
      <div id="scene">
        <div data-depth="0.00" class="layer"></div>
        <div data-depth="0.88" class="stars-cluster stars-cluster-1 layer">
          <img src="../../static/img/banner_icon03.png" class="banner-icon03">
        </div>
        <div data-depth="0.99" class="moon-wrapper layer">
          <img src="../../static/img/banner_icon02.png" class="banner-icon02">
        </div>
        <div data-depth="0.66" class="title-wrapper layer">
          <img src="../../static/img/banner_icon01.png" class="banner-icon01">
        </div>
        <div data-depth="0.77" class="title-wrapper layer">
          <div class="btn">
            <router-link to="/index"  class="btn-left">爱情誓言</router-link>
            <router-link to="/myLove"  class="btn-right">我的宣言</router-link>
          </div>
        </div>
      </div>
    </div>
    <!--/banner-->

    <div class="row my-oath issue-an-oath">
      <h2>发布誓言</h2>
      <form @submit="onSubmit" @reset="onReset">
        <div class="thumbnail">
          <div class="imageFileInput">
            <input id = "uploadImage" class="fileInput" type="file" v-on:change="getFile">
            <div class="upload">上传图片</div>
          </div>
        </div>
        <div class="caption">
          <div class="form-group">
            <label for="exampleInputEmail1">爱情宣言主题*</label>
            <input type="text" class="form-control" v-model="form.title">
          </div>
          <div class="form-group">
            <label for="exampleInputEmail1">爱情宣言内容*</label>
            <textarea class="form-control" rows="3" v-model="form.message"></textarea>
          </div>
          <div class="form-group">
            <button type="reset" class="btn">重置</button>
            <button type="submit" class="btn btn-default">发布誓言</button>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>
<script >
import api from '../libs/api'
import image from '../libs/image'

export default {
  name: 'CreateLove',
  data () {
    return {
      form: {
        title: '',
        message: '',
        file: ''
      }
    }
  },
  methods: {
    onSubmit (evt) {
      evt.preventDefault()
      image.PhotoCompress(this.form.file, {
        quality: 0.8,
        width: 640
      }, re => {
        api.publishLove(this.form.title, this.form.message, re, resp => {
        })
      })
    },
    onReset (evt) {
      evt.preventDefault()
      this.form.title = ''
      this.form.message = ''
      this.form.file = ''
    },
    getFile($event){
      this.form.file = $event.target.files[0] //获取要上传的文件
    }
  }
}
</script>



